<template>
  <div>
    <slot name="before"></slot>
    <div :id="elId"
      :style="style"></div>
    <slot name="after"></slot>
  </div>
</template>

<script>
// 
import echarts from 'echarts/lib/echarts'
import 'echarts/lib/chart/bar'
// 
import 'echarts/lib/component/tooltip'
import 'echarts/lib/component/title'
import 'echarts/lib/component/legend'
import 'echarts/lib/component/toolbox'
import 'zrender/lib/svg/svg' //svg

//uuid
import uuidv1 from 'uuid/v1'
export default {
  name: 'chart-bar',
  props: {
    width: { type: String, default: '100%' },
    height: { type: String, default: '300px' },
    color: {
      type: [String, Object],
      default() {
        return new echarts.graphic.LinearGradient(0, 0, 0, 1, [
          { offset: 0, color: '#01edc3' }, // 0% 
          { offset: 1, color: '#07caa6' } // 100% 
        ])
      }
    },
    xAxisDate: {
      type: Array,
      default() {
        return []
      }
    },
    seriesData: {
      type: Array,
      default() {
        return []
      }
    },
    title: {
      type: String,
      default: ''
    },
    unit: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      elId: ''
    }
  },
  created() {
    this.elId = uuidv1() //id
  },
  computed: {
    style() {
      return {
        height: this.height,
        width: this.width
      }
    }
  },

  mounted() {
    const mytextStyle = {
      color: '#466791', //
      fontStyle: 'normal', //italic  oblique
      fontWeight: 'normal', //bold   bolder   lighter  100 | 200 | 300 | 400...
      fontFamily: 'MicrosoftYaHei', //
      fontSize: 14 //
    }
    const mylineStyle = {
      color: '#000', //'rgb(128, 128, 128)''rgba(128, 128, 128, 0.5)'
      shadowColor: 'red', //
      type: 'dotted', //soliddasheddotted
      width: 2, //
      opacity: 0.06 // 0  1  0 
    }
    const legendData = []
    legendData.push(this.title)
    const option = {
      animation: false,
      color: this.color,
      tooltip: {
        trigger: 'item', // axis
        axisPointer: {
          shadowStyle: {
            color: 'rgba(98, 167, 255, 0.3)'
          }
        },
        extraCssText: 'max-width:320px;white-space:pre-wrap'
      },
      grid: {
        left: '3%',
        right: '4%',
        bottom: '3%',
        containLabel: true
      },
      xAxis: [
        {
          type: 'category',
          data: this.xAxisDate,
          axisLine: false,
          splitLine: false,
          axisLabel: {
            textStyle: mytextStyle,
            interval: 0,
            formatter(value) {
              let val = []
              for (let i = 0; i < value.length; i += 5) {
                val.push(value.substr(i, 5))
              }
              let content = val.join('\n')
              return value.length >= 27 ? `${content.slice(0, 27)}...` : content
            }
          }
        }
      ],
      yAxis: [
        {
          type: 'value',
          axisLine: false,
          splitLine: {
            // grid 
            lineStyle: mylineStyle
          },
          axisLabel: {
            textStyle: mytextStyle,
            formatter: `{value} ${this.unit}`
          }
        }
      ],
      series: [
        {
          name: this.title,
          type: 'bar',
          barWidth: '60%',
          label: {
            show: true, //
            position: 'top', //
            formatter: `{c} ${this.unit}`,
            fontSize: 14,
            color: '#ccc'
          },

          data: this.seriesData
        }
      ]
    }
    const chartObj = echarts.init(document.getElementById(this.elId), null, { renderer: 'svg' })
    chartObj.setOption(option)
  }
}
</script>

